body {
    font-family: Montserrat, sans-serif;
    background: linear-gradient(to right, #f7f7ef, #fab47b);
    color: #333;
    margin: 0;
    padding: 0;
}

header {
    background: rgba(0, 0, 0, 0.1);
    padding: 10px 0;
}

nav ul {
    display: flex;
    list-style: none;
    justify-content: center;
    padding: 0;
}

nav ul li {
    margin: 0 15px;
}

nav ul li a {
    text-decoration: none;
    color: black;
    font-weight: bold;
}

h1 {
    text-align: center;
    font-size: 2em;
    margin-top: 20px;
}

p {
    text-align: center;
    font-size: 1.2em;
    max-width: 600px;
    margin: 10px auto;
}

img {
    display: block;
    margin: 20px auto;
    border-radius: 10px;
}